import Vue from 'vue';
import App from './App.vue';
import VueRouter from "vue-router";
Vue.use(VueRouter);
import VueResource from 'vue-resource'
Vue.use(VueResource);
const router = new VueRouter({
      // mode: 'history',//history 模式可以让 url 更像一个链接，然而需要后台做一些设置，page 服务不能做后台设置，所以把这句去了就可以了。
      routes: [
            {
                  path: '/',
                  component: resolve => require(['./components/automatic.vue'], resolve)
            }
      ]
})


new Vue({
      router:router,
      el: '#app',
      render: h => h(App)
})



